<template>
  <button class="backBtn" @click="$emit('toggleShowResults')">back</button>

  <div v-for="rakija in chosenRakije" :key="rakija.name" class="rakija">
    <p>{{ rakija.name }}</p>
    <p>x {{ rakija.count }}</p>
  </div>
</template>

<script>
import { computed } from "@vue/runtime-core";
export default {
  name: "RakijaResults",
  props: ["rakije"],
  setup(props) {
    const chosenRakije = computed(() => {
      return props.rakije.filter((rakija) => {
        return rakija.count > 0;
      });
    });

    const handleStorage = () => {
      let filteredRakije = [];
      const today = new Date();
      const date =
        ("0" + today.getDate()).slice(-2) +
        "/" +
        ("0" + today.getMonth()).slice(-2) +
        "/" +
        today.getFullYear();
      const time =
        ("0" + today.getHours()).slice(-2) +
        ":" +
        ("0" + today.getMinutes()).slice(-2);
      const dateTime = date + " " + time;

      props.rakije.forEach((rakija) => {
        if (rakija.count > 0) {
          filteredRakije.push({
            count: rakija.count,
            name: rakija.name,
            time: dateTime,
          });
        }
      });

      const rakijaHistory = JSON.parse(localStorage.getItem("rakijaHistory"));
      if (rakijaHistory) {
        rakijaHistory.push(filteredRakije);
        localStorage.setItem("rakijaHistory", JSON.stringify(rakijaHistory));
      } else {
        let temp = [];
        temp.push(filteredRakije);
        localStorage.setItem("rakijaHistory", JSON.stringify(temp));
      }
    };
    handleStorage();

    return { chosenRakije };
  },
};
</script>

