<template>
  <Transition name="page" mode="out-in">
    <div v-if="!showResults">
      <Header />
      <RakijaPicker @toggleShowResults="toggleShowResults" />
    </div>
    <div v-else>
      <RakijaResults :rakije="rakije" @toggleShowResults="toggleShowResults" />
    </div>
  </Transition>
</template>

<script>
import { ref } from "@vue/reactivity";
// @ is an alias to /src
import Header from "../components/Header.vue";
import RakijaPicker from "../components/RakijaPicker.vue";
import RakijaResults from "../components/RakijaResults.vue";

export default {
  name: "HomeView",
  components: {
    Header,
    RakijaPicker,
    RakijaResults,
  },
  setup() {
    const showResults = ref(false);

    const rakije = ref([]);

    const toggleShowResults = (rakijas) => {
      showResults.value = !showResults.value;
      if (showResults.value) {
        rakije.value = rakijas;
      }
    };

    return { showResults, rakije, toggleShowResults };
  },
};
</script>
<style scoped>
.page-enter-active,
.page-leave-active {
  transition: all 200ms ease-out;
}
.page-enter-from {
  opacity: 0;
  transform: translateX(50%);
}
.page-leave-to {
  opacity: 0;
  transform: translateX(-50%);
}
</style>
