<template>
  <main>
    <TransitionGroup name="cards" appear @enter="fadeInCards">
      <div
        class="card"
        v-for="(rakija, index) in rakijaHistory"
        :key="index"
        :data-index="index"
      >
        <p class="date">{{ rakija[0].time }}</p>
        <ul>
          <li v-for="(rak, index) in rakija" :key="index + 100">
            {{ rak.name }} x {{ rak.count }}
          </li>
        </ul>
      </div>
    </TransitionGroup>
    <button @click="clearStorage">Clear History</button>
  </main>
</template>

<script>
import gsap from "gsap";
import { ref } from "@vue/reactivity";
export default {
  name: "HistoryGrid",
  setup() {
    const rakijaHistory = ref(
      JSON.parse(localStorage.getItem("rakijaHistory"))
    );

    const fadeInCards = (el, done) => {
      gsap.from(el, {
        opacity: 0,
        x: "-100%",
        duration: 0.75,
        delay: el.dataset.index * 0.2,
        onComplete: done,
      });
    };

    const clearStorage = () => {
      localStorage.removeItem("rakijaHistory");
      rakijaHistory.value = [];
    };

    return { rakijaHistory, fadeInCards, clearStorage };
  },
};
</script>

