<template>
  <div class="status">
    <p :class="{ allSelected: sum % 10 == 0 && sum }">{{ sum }}/{{ max }}</p>
    <button
      :class="{ enabledBtn: sum % 10 == 0 && sum }"
      @click="$emit('toggleShowResults', rakijas)"
      :disabled="sum != max"
    >
      DONE!
    </button>
  </div>
  <div class="picker">
    <div
      class="rakija"
      :class="{ even: index % 2, disabled: !rakija.inStock }"
      v-for="(rakija, index) in rakijas"
      :key="rakija.name"
    >
      <div class="rakija-left">
        <p class="name">{{ rakija.name }}</p>
        <p class="desc">{{ rakija.description }}</p>
      </div>
      <div class="rakija-right">
        <img
          src="../assets/sub.svg"
          alt="deduct"
          @click="minusOne(rakija)"
          :class="{ disabled: rakija.count <= 0 }"
        />
        <p>{{ rakija.count }}</p>
        <img
          src="../assets/add.svg"
          alt="add"
          @click="addOne(rakija)"
          :class="{ disabled: rakija.count >= 99 }"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
export default {
  name: "RakijaPicker",
  setup() {
    const rakijas = ref([
      {
        name: "Borovnica",
        description: "crna kraljica",
        count: 0,
        inStock: true,
      },
      {
        name: "Medica",
        description: "ubod pčele",
        count: 0,
        inStock: true,
      },
      {
        name: "Višnja",
        description: "za tetkice",
        count: 0,
        inStock: true,
      },
      {
        name: "Suha šljiva",
        description: "za potenciju",
        count: 0,
        inStock: true,
      },
      {
        name: "Bazga",
        description: "lijep ten",
        count: 0,
        inStock: true,
      },
      {
        name: "Kruška",
        description: "a nije Vilja",
        count: 0,
        inStock: true,
      },
      {
        name: "Jabuka cimet",
        description: "a ljeti, a zimi...",
        count: 0,
        inStock: false,
      },
      {
        name: "Limoncello",
        description: "čisti vitamin",
        count: 0,
        inStock: true,
      },
      {
        name: "Jagodička",
        description: "miss rakija",
        count: 0,
        inStock: true,
      },
      {
        name: "Rogač",
        description: "no signal",
        count: 0,
        inStock: true,
      },
      {
        name: "Smokva",
        description: "za uzemljenje",
        count: 0,
        inStock: true,
      },
      {
        name: "Loza",
        description: "insomnia",
        count: 0,
        inStock: true,
      },
      {
        name: "Šljivko",
        description: "čisti slavonac",
        count: 0,
        inStock: true,
      },
      {
        name: "Istarska trava",
        description: "strava",
        count: 0,
        inStock: true,
      },
      {
        name: "Orah",
        description: "a nije slavonski..",
        count: 0,
        inStock: true,
      },
      {
        name: "Lavanda",
        description: "nježni pupoljak",
        count: 0,
        inStock: true,
      },
      {
        name: "Peperinjo",
        description: "nije od papra, od mente je...",
        count: 0,
        inStock: true,
      },
      {
        name: "Pelin i med",
        description: "savršen duo",
        count: 0,
        inStock: true,
      },
      {
        name: "Jabuka",
        description: "ne pada daleko..",
        count: 0,
        inStock: true,
      },
      {
        name: "Biska",
        description: "she lost control",
        count: 0,
        inStock: true,
      },
    ]);

    // methods:
    const addOne = (rakija) => {
      if (rakija.count < 99 && rakija.inStock) {
        rakija.count++;
      }
    };
    const minusOne = (rakija) => {
      if (rakija.count > 0 && rakija.inStock) {
        rakija.count--;
      }
    };

    // computed
    const sum = computed(() => {
      let test = 0;
      rakijas.value.forEach((rakija) => {
        test += rakija.count;
      });
      return test;
    });
    const max = computed(() => {
      if (sum.value == 0) {
        return 10;
      }
      return (Math.floor((sum.value - 0.1) / 10) + 1) * 10;
    });

    //return
    return { rakijas, sum, max, addOne, minusOne };
  },
};
</script>

